@import "src/components/constants";

.partners-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(2, 1fr); }

.div1 {
  grid-area: 1 / 1 / 3 / 2; }

.div2 {
  grid-area: 1 / 2 / 2 / 3; }

.div3 {
  grid-area: 2 / 2 / 3 / 3;
  box-sizing: border-box;
  text-align: left;

  .header {
    display: flex;
    flex-direction: row;
    margin-top: 20px;

    & > * {
      margin-left: 20px; }

    .partners-titles {
      justify-content: center;
      p {
        @include single-line-text(2.2rem);
        margin-top: 5px; }

      h3 {
        @include single-line-text(2.1rem);
        font-weight: 500; } } }


  & > p {
    $left: 15px !global;
    padding: 15px 0 0 $left;
    font-size: 1rem; }

  .urls {
    margin-left: $left;
    font-size: 1.1rem; } }
